// Accept header variations
const ACCEPT_HEADERS = [
    'application/json, text/plain, */*',
    'application/json',
    '*/*',
    'application/json, text/javascript, */*; q=0.01',
];

// Sec-Fetch headers (modern browsers)
const SEC_FETCH_MODES = ['cors', 'no-cors', 'navigate'];
const SEC_FETCH_SITES = ['cross-site', 'same-origin', 'same-site', 'none'];
const SEC_FETCH_DESTS = ['document', 'empty', 'script'];

// Cache-Control variations
const CACHE_CONTROLS = [
    'no-cache',
    'max-age=0',
    'no-store',
    undefined, // Some requests don't have this header
];

// Connection header variations
const CONNECTIONS = ['keep-alive', undefined];

// Referer URLs (Roblox-related)
const REFERERS = [
    'https://www.roblox.com/',
    'https://www.roblox.com/users/profile',
    'https://www.roblox.com/games',
    'https://www.roblox.com/discover',
    'https://www.roblox.com/home',
    undefined, // Some requests don't have referer
];

/**
 * Get a random element from an array
 */
function randomFrom<T>(arr: T[]): T {
    return arr[Math.floor(Math.random() * arr.length)];
}

/**
 * Generate random fingerprint headers to mimic a real user
 */
export function generateRandomHeaders(): Record<string, string> {
    const headers: Record<string, string> = {};

    // Core headers - always include
    headers['User-Agent'] = randomFrom(USER_AGENTS);
    headers['Accept-Language'] = randomFrom(ACCEPT_LANGUAGES);
    headers['Accept'] = randomFrom(ACCEPT_HEADERS);

    // Optional headers - include randomly
    const cacheControl = randomFrom(CACHE_CONTROLS);
    if (cacheControl) {
        headers['Cache-Control'] = cacheControl;
    }

    const connection = randomFrom(CONNECTIONS);
    if (connection) {
        headers['Connection'] = connection;
    }

    const referer = randomFrom(REFERERS);
    if (referer) {
        headers['Referer'] = referer;
    }

    // Sec-Fetch headers (50% chance to include - not all browsers send these)
    if (Math.random() > 0.5) {
        headers['Sec-Fetch-Mode'] = randomFrom(SEC_FETCH_MODES);
        headers['Sec-Fetch-Site'] = randomFrom(SEC_FETCH_SITES);
        headers['Sec-Fetch-Dest'] = randomFrom(SEC_FETCH_DESTS);
    }

    // DNT (Do Not Track) - random
    if (Math.random() > 0.7) {
        headers['DNT'] = '1';
    }

    // Upgrade-Insecure-Requests - random (usually for navigation)
    if (Math.random() > 0.8) {
        headers['Upgrade-Insecure-Requests'] = '1';
    }

    return headers;
}

/**
 * Generate random delay with human-like variation
 * Humans don't click at exact intervals - there's natural variation
 */
export function getHumanLikeDelay(baseDelay: number = 500): number {
    // Add random jitter: 70-130% of base delay
    const jitterMultiplier = 0.7 + (Math.random() * 0.6);
    const delay = Math.round(baseDelay * jitterMultiplier);

    // Occasionally add longer pause (like a human reading)
    if (Math.random() > 0.9) {
        return delay + Math.round(Math.random() * 2000); // Extra 0-2 seconds
    }

    return delay;
}

/**
 * Generate a unique request ID (some APIs track this)
 */
export function generateRequestId(): string {
    const chars = 'abcdefghijklmnopqrstuvwxyz0123456789';
    let id = '';
    for (let i = 0; i < 32; i++) {
        id += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return id;
}

/**
 * Get current timestamp with slight randomization
 * (Some fingerprinting tracks exact request timing)
 */
export function getRandomizedTimestamp(): number {
    return Date.now() + Math.floor(Math.random() * 1000) - 500;
}

/**
 * Generate a browser tracker ID (required by some Roblox API endpoints)
 * Format: Large numeric value that Roblox uses to track browser sessions
 * Example: 65445383610
 */
export function generateBrowserTrackerId(): string {
    // Generate a large random number (10-12 digits like real Roblox tracker IDs)
    const min = 10000000000; // 10 billion
    const max = 99999999999; // ~100 billion
    return String(Math.floor(Math.random() * (max - min) + min));
}

/**
 * Generate Roblox-specific headers that match what a real browser sends
 * Based on actual captured request from Chrome 143 (February 2026)
 * These headers closely match the format seen in real blocking requests
 */
export function generateRobloxHeaders(): Record<string, string> {
    const browserTrackerId = generateBrowserTrackerId();
    const acceptLanguage = randomFrom(ACCEPT_LANGUAGES);

    return {
        'Accept': 'application/json, text/plain, */*',
        'Accept-Encoding': 'gzip, deflate, br',
        'Accept-Language': acceptLanguage,
        'Origin': 'https://www.roblox.com',
        'Referer': 'https://www.roblox.com/',
        'Priority': 'u=1, i',
        'Sec-Ch-Ua': '"Chromium";v="143", "Not A(Brand";v="24"',
        'Sec-Ch-Ua-Mobile': '?0',
        'Sec-Ch-Ua-Platform': '"Windows"',
        'Sec-Fetch-Dest': 'empty',
        'Sec-Fetch-Mode': 'cors',
        'Sec-Fetch-Site': 'same-site',
        'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/143.0.0.0 Safari/537.36',
        'x-browser-tracker-id': browserTrackerId,
    };
}
