import { NextRequest, NextResponse } from 'next/server';
import { getCurrentUser } from '@/lib/auth';
import { supabase } from '@/lib/supabase';
import { getUserInfo } from '@/lib/roblox';

// GET blacklist
export async function GET() {
    try {
        const currentUser = await getCurrentUser();

        if (!currentUser?.is_admin) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 403 });
        }

        const { data: blacklist } = await supabase
            .from('roblox_blacklist')
            .select('*')
            .order('created_at', { ascending: false });

        return NextResponse.json({ blacklist: blacklist || [] });
    } catch (error) {
        console.error('Error fetching blacklist:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}

// POST add to blacklist
// Accepts either username or user ID - auto-resolves user ID to username
export async function POST(request: NextRequest) {
    try {
        const currentUser = await getCurrentUser();

        if (!currentUser?.is_admin) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 403 });
        }

        const { input, reason } = await request.json();

        if (!input || !input.trim()) {
            return NextResponse.json({ error: 'Username or User ID is required' }, { status: 400 });
        }

        const trimmedInput = input.trim();
        let username: string;
        let robloxUserId: string | null = null;

        // Check if input is numeric (user ID) or alphanumeric (username)
        if (/^\d+$/.test(trimmedInput)) {
            // Input is a user ID - resolve to username via Roblox API
            const userId = parseInt(trimmedInput, 10);
            const userInfo = await getUserInfo(userId);

            if (!userInfo) {
                return NextResponse.json({ error: 'Could not find Roblox user with that ID' }, { status: 404 });
            }

            username = userInfo.name;
            robloxUserId = String(userId);
        } else {
            // Input is a username
            username = trimmedInput;
        }

        const { error } = await supabase
            .from('roblox_blacklist')
            .upsert({
                username: username.toLowerCase(),
                roblox_user_id: robloxUserId,
                reason,
                added_by: currentUser.username,
            }, { onConflict: 'username' });

        if (error) {
            console.error('Supabase error:', error);
            return NextResponse.json({ error: 'Failed to add to blacklist' }, { status: 500 });
        }

        return NextResponse.json({
            success: true,
            username: username.toLowerCase(),
            robloxUserId
        });
    } catch (error) {
        console.error('Error adding to blacklist:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}

// DELETE remove from blacklist
export async function DELETE(request: NextRequest) {
    try {
        const currentUser = await getCurrentUser();

        if (!currentUser?.is_admin) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 403 });
        }

        const { username } = await request.json();

        if (!username) {
            return NextResponse.json({ error: 'Username is required' }, { status: 400 });
        }

        await supabase
            .from('roblox_blacklist')
            .delete()
            .eq('username', username.toLowerCase());

        return NextResponse.json({ success: true });
    } catch (error) {
        console.error('Error removing from blacklist:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}

