// Advanced Rate Limiter Utility
// Implements: Throttling, Exponential Backoff, Jitter, Batch Processing

/**
 * Sleep for a given number of milliseconds
 */
export function sleep(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms));
}

/**
 * Add random jitter to a delay to avoid detection patterns
 * @param baseDelay - Base delay in ms
 * @param jitterPercent - Jitter percentage (0-1), default 0.3 (30%)
 */
export function addJitter(baseDelay: number, jitterPercent: number = 0.3): number {
    const jitter = baseDelay * jitterPercent * (Math.random() * 2 - 1);
    return Math.max(0, baseDelay + jitter);
}

/**
 * Calculate exponential backoff delay
 * @param attempt - Current attempt number (0-indexed)
 * @param baseDelay - Base delay in ms, default 1000
 * @param maxDelay - Maximum delay in ms, default 30000
 */
export function exponentialBackoff(attempt: number, baseDelay: number = 1000, maxDelay: number = 30000): number {
    const delay = Math.min(baseDelay * Math.pow(2, attempt), maxDelay);
    return addJitter(delay);
}

interface RetryOptions {
    maxRetries?: number;
    baseDelay?: number;
    maxDelay?: number;
    onRetry?: (attempt: number, error: Error, delay: number) => void;
}

/**
 * Execute a function with retry and exponential backoff
 */
export async function withRetry<T>(
    fn: () => Promise<T>,
    options: RetryOptions = {}
): Promise<T> {
    const {
        maxRetries = 3,
        baseDelay = 1000,
        maxDelay = 30000,
        onRetry
    } = options;

    let lastError: Error | null = null;

    for (let attempt = 0; attempt <= maxRetries; attempt++) {
        try {
            return await fn();
        } catch (error) {
            lastError = error as Error;

            // Check if it's a rate limit error (429)
            const isRateLimit = error instanceof Error &&
                (error.message.includes('429') || error.message.includes('rate limit'));

            if (attempt < maxRetries) {
                const delay = isRateLimit
                    ? exponentialBackoff(attempt, baseDelay * 2, maxDelay) // Longer delay for rate limits
                    : exponentialBackoff(attempt, baseDelay, maxDelay);

                if (onRetry) {
                    onRetry(attempt + 1, lastError, delay);
                }

                console.log(`Retry ${attempt + 1}/${maxRetries} after ${delay}ms...`);
                await sleep(delay);
            }
        }
    }

    throw lastError;
}

interface ThrottleOptions {
    minDelay?: number;      // Minimum delay between requests (ms)
    maxDelay?: number;      // Maximum delay for backoff (ms)
    requestsPerSecond?: number;  // Target requests per second
}

/**
 * Creates a throttled request queue that respects rate limits
 */
export function createThrottledQueue(options: ThrottleOptions = {}) {
    const {
        minDelay = 100,
        maxDelay = 5000,
        requestsPerSecond = 5
    } = options;

    const delayPerRequest = 1000 / requestsPerSecond;
    let lastRequestTime = 0;
    let consecutiveErrors = 0;
    let currentDelay = Math.max(minDelay, delayPerRequest);

    return {
        /**
         * Execute a request with throttling
         */
        async execute<T>(fn: () => Promise<T>): Promise<T> {
            // Calculate time since last request
            const now = Date.now();
            const timeSinceLastRequest = now - lastRequestTime;

            // Wait if needed
            if (timeSinceLastRequest < currentDelay) {
                await sleep(currentDelay - timeSinceLastRequest + addJitter(50));
            }

            lastRequestTime = Date.now();

            try {
                const result = await fn();

                // Success - gradually reduce delay
                consecutiveErrors = 0;
                currentDelay = Math.max(minDelay, currentDelay * 0.9);

                return result;
            } catch (error) {
                consecutiveErrors++;

                // Increase delay on errors
                currentDelay = Math.min(maxDelay, currentDelay * 2);

                throw error;
            }
        },

        /**
         * Get current delay for monitoring
         */
        getCurrentDelay(): number {
            return currentDelay;
        },

        /**
         * Reset the queue state
         */
        reset(): void {
            consecutiveErrors = 0;
            currentDelay = Math.max(minDelay, delayPerRequest);
            lastRequestTime = 0;
        }
    };
}

interface BatchOptions<T, R> {
    items: T[];
    batchSize?: number;
    delayBetweenBatches?: number;
    onProgress?: (completed: number, total: number) => void;
    processor: (item: T) => Promise<R>;
}

/**
 * Process items in batches with delays
 */
export async function processBatch<T, R>(options: BatchOptions<T, R>): Promise<R[]> {
    const {
        items,
        batchSize = 10,
        delayBetweenBatches = 2000,
        onProgress,
        processor
    } = options;

    const results: R[] = [];
    const total = items.length;
    let completed = 0;

    // Split into batches
    for (let i = 0; i < items.length; i += batchSize) {
        const batch = items.slice(i, i + batchSize);

        // Process batch concurrently
        const batchResults = await Promise.all(
            batch.map(async (item) => {
                try {
                    const result = await processor(item);
                    completed++;

                    if (onProgress) {
                        onProgress(completed, total);
                    }

                    return result;
                } catch (error) {
                    completed++;
                    console.error('Batch item failed:', error);
                    return null as R;
                }
            })
        );

        results.push(...batchResults);

        // Delay between batches (except for the last one)
        if (i + batchSize < items.length) {
            console.log(`Batch ${Math.floor(i / batchSize) + 1} complete. Waiting ${delayBetweenBatches}ms...`);
            await sleep(addJitter(delayBetweenBatches));
        }
    }

    return results;
}

/**
 * Sequential processing with delay between each item
 * Better for strict rate limits
 */
export async function processSequentially<T, R>(
    items: T[],
    processor: (item: T) => Promise<R>,
    delayBetweenItems: number = 200,
    onProgress?: (completed: number, total: number) => void
): Promise<R[]> {
    const results: R[] = [];
    const total = items.length;

    for (let i = 0; i < items.length; i++) {
        try {
            const result = await processor(items[i]);
            results.push(result);
        } catch (error) {
            console.error(`Item ${i} failed:`, error);
            results.push(null as R);
        }

        if (onProgress) {
            onProgress(i + 1, total);
        }

        // Delay between items (except for the last one)
        if (i < items.length - 1) {
            await sleep(addJitter(delayBetweenItems));
        }
    }

    return results;
}
